@import "~styles/variables";

.about {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    &__row {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 1.5rem;
    }
    &__image {
        margin-right: 1.5rem;
        margin-bottom: 1.5rem;
        flex: 1 1 15vw;
        img {
            display: block;
            margin: auto;
            width: 15vw;
        }
    }
    &__info {
        flex: 100000 1 512px;
        padding: 0 2rem 2rem;
    }
}
