@import "~styles/variables";

.project-card {
	display: flex;
	flex-wrap: wrap;

	position: relative;
	min-height: 12rem;

	text-decoration: none;
	color: $dark;
	&__image{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	h3 {
		color: darken($primary, 40%);
	}
	&__description{
		flex: 1 1 24rem;
		padding: 0 1rem;
        border-top: 0.5rem solid $card-shadow-color;
		color: darken($primary, 40%);
	}
