@import "~styles/variables";

footer {
	padding: 1.25rem 2rem;
	height: $footer-height;
	background-color:lighten($dark, 5%);
	box-shadow: 0 0 8px black;
	text-align: center;
	h3 {
		display: inline;
		margin-right: 0.5rem;
		color: darken($light,15%);

	}
	ul {
		display: inline;
		margin: 0;
		padding: 0;
	}
	li {
		display: inline;
		margin: 0 0.5rem;
	}
	a {
		font-weight: bold;
		color: lighten($tertiary, 15%);
		&:visited {
			color: $secondary;
		}
	}

}
