@import "~styles/variables";
@import "~styles/mixins";

.header {
	position: relative;
	height: $header-height;
	background-color: lighten($dark, 7.5%);
	box-shadow: 0 0 8px black;
	&::after {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		width: 100%;
		height: 3rem;
		background-color: $side-triangles-color;
		box-shadow: 0 0 4px black;
		transform-origin: right bottom;
		transform: translateY(100%) rotate(5deg) skew(5deg);
		z-index: -1;
	}
}

.header__nav {
	display: flex;
	.logo {
		display: inline-block;
		margin: 0.5rem;
		width: 3rem;
		height: 3rem;
		background-color: #fff;
		img {
			width: 100%;
			height: 100%;
		}
	}
	.link {
		display: inline-block;
		padding: 0.75rem 1rem;
		margin: 0.5rem;
		height: 3rem;
		color: $primary;
		text-decoration: none;
		@include font-heading();
		transition: 0.3s;
		&:hover {
			background-color: lighten($dark, 20%);
		}
		position: relative;
		&::before {
			content: "";
			position: absolute;
			display: block;
			top: calc(100%);
			height: 1rem;
			left: 0;
			width: 100%;
			opacity: 0;
			background: linear-gradient(0deg, $tertiary, rgba(0,0,0,0));
			transition: 0.3s;
		}
		&_active::before {
			top: calc(100% - 0.5rem);
			opacity: 1;
		}
	}
}
