@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.card {
  background-color: #fff;
  box-shadow: 1rem 0.5rem 0 #0025f8; }
  .card_post {
    padding: 1rem 2rem;
    width: 100%; }

.card-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(18rem, 1fr));
  grid-gap: 2rem; }

.about {
  display: flex;
  flex-direction: column;
  padding: 2rem; }
  .about__row {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 1.5rem; }
  .about__image {
    margin-right: 1.5rem;
    margin-bottom: 1.5rem;
    flex: 1 1 15vw; }
    .about__image img {
      display: block;
      margin: auto;
      width: 15vw; }
  .about__info {
    flex: 100000 1 512px;
    padding: 0 2rem 2rem; }

.content {
  position: relative;
  min-height: calc(100vh - 8rem);
  overflow: hidden; }
  .content__inner {
    margin: auto;
    padding: 2rem 0;
    width: 75%; }
  .content::before {
    content: "";
    display: block;
    position: absolute;
    top: 100%;
    width: 100%;
    height: 2rem;
    background-color: #a000ac;
    box-shadow: 0 0 4px black;
    -webkit-transform-origin: left bottom;
            transform-origin: left bottom;
    -webkit-transform: translateY(-100%) rotate(5deg) skew(5deg);
            transform: translateY(-100%) rotate(5deg) skew(5deg);
    z-index: -1; }

footer {
  padding: 1.25rem 2rem;
  height: 4rem;
  background-color: #2c3134;
  box-shadow: 0 0 8px black;
  text-align: center; }
  footer h3 {
    display: inline;
    margin-right: 0.5rem;
    color: #d9d9d9; }
  footer ul {
    display: inline;
    margin: 0;
    padding: 0; }
  footer li {
    display: inline;
    margin: 0 0.5rem; }
  footer a {
    font-weight: bold;
    color: #4661ff; }
    footer a:visited {
      color: #e700f8; }

.header {
  position: relative;
  height: 4rem;
  background-color: #31383b;
  box-shadow: 0 0 8px black; }
  .header::after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 3rem;
    background-color: #a000ac;
    box-shadow: 0 0 4px black;
    -webkit-transform-origin: right bottom;
            transform-origin: right bottom;
    -webkit-transform: translateY(100%) rotate(5deg) skew(5deg);
            transform: translateY(100%) rotate(5deg) skew(5deg);
    z-index: -1; }

.header__nav {
  display: flex; }
  .header__nav .logo {
    display: inline-block;
    margin: 0.5rem;
    width: 3rem;
    height: 3rem;
    background-color: #fff; }
    .header__nav .logo img {
      width: 100%;
      height: 100%; }
  .header__nav .link {
    display: inline-block;
    padding: 0.75rem 1rem;
    margin: 0.5rem;
    height: 3rem;
    color: #f8b100;
    text-decoration: none;
    font-family: Montserrat, Roboto;
    font-size: 1.25rem;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    position: relative; }
    .header__nav .link:hover {
      background-color: #4f585d; }
    .header__nav .link::before {
      content: "";
      position: absolute;
      display: block;
      top: calc(100%);
      height: 1rem;
      left: 0;
      width: 100%;
      opacity: 0;
      background: -webkit-gradient(linear, left bottom, left top, from(#0025f8), to(rgba(0, 0, 0, 0)));
      background: -webkit-linear-gradient(bottom, #0025f8, rgba(0, 0, 0, 0));
      background: linear-gradient(0deg, #0025f8, rgba(0, 0, 0, 0));
      -webkit-transition: 0.3s;
      transition: 0.3s; }
    .header__nav .link_active::before {
      top: calc(100% - 0.5rem);
      opacity: 1; }

.project-card {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  min-height: 12rem;
  text-decoration: none;
  color: #202426; }
  .project-card__image {
    width: 100%;
    height: 100%;
    object-fit: cover; }
  .project-card h3 {
    color: #2c1f00; }
  .project-card__description {
    flex: 1 1 24rem;
    padding: 0 1rem;
    border-top: 0.5rem solid #0025f8;
    color: #2c1f00; }

* {
  box-sizing: border-box; }

body {
  background: #202426;
  font-family: Montserrat, Roboto; }

h1, h2, h3, h4, h5 {
  color: #202426; }

a {
  color: #0025f8; }
  a:visited {
    color: #e700f8; }

