@import "styles/variables";

.content {
	position: relative;
	min-height: calc(100vh - 8rem);
	overflow: hidden;
	&__inner {
		margin: auto;
		padding: 2rem 0;
		width: 75%;
	}
	&::before {
		content: "";
		display: block;
		position: absolute;
		top: 100%;
		width: 100%;
		height: 2rem;
		background-color: $side-triangles-color;
		box-shadow: 0 0 4px black;
		transform-origin: left bottom;
		transform: translateY(-100%) rotate(5deg) skew(5deg);
		z-index: -1;
	}
}
