@import "~styles/variables";
@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700&display=swap');

* {
	box-sizing: border-box;
}

body {
	background: $dark;
	font-family: Montserrat, Roboto;
}

h1,h2,h3,h4,h5 {
	color: $dark
}

a {
	color: $tertiary;
	&:visited {
		color: $secondary;
	}
}
